@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;500;700&display=swap');

// colors
$Marine_blue: hsl(213, 96%, 18%);
$Purplish_blue: hsl(243, 100%, 62%);
$Pastel_blue: hsl(228, 100%, 84%);
$Light_blue: hsl(206, 94%, 87%);
$Strawberry_red: hsl(354, 84%, 57%);

$Cool_gray: hsl(231, 11%, 63%);
$Light_gray: hsl(229, 24%, 87%);
$Magnolia: hsl(217, 100%, 97%);
$Alabaster: hsl(231, 100%, 99%);
$White: hsl(0, 0%, 100%);

* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
html {
  font-family: 'Ubuntu', sans-serif;
  background-color: $Magnolia;
}

ul li {
  list-style: none;
}
@mixin mobile {
  @media screen and (max-width: 500px) {
    @content;
  }
}

#root {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  @include mobile{
    height: 95vh;
  }
}

// Responisve mixins


// Utilities
.card {
  border-radius: 10px;
  padding: 1rem;
}

.input {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  position: relative;
  input {
    width: 100%;
    border: 1px solid $Cool_gray;
    padding: .8rem;
    border-radius: 5px;
    font-size: 14px;
    outline: none;
    color: $Marine_blue;
    font-family: 'Ubuntu', sans-serif;
    &::placeholder {
      color: $Cool_gray;
    }
    &:focus {
      border: 1px solid $Purplish_blue;
    }
  }
  label {
    margin-bottom: 5px;
    color: $Marine_blue;
  }  
  &.error{
    small{
      color: red;
      position: absolute;
      top: 0;
      right: 0;
    }
    input{
      border-color: red;
    }
  }
}

.button {
  padding: 0.5rem 1rem;
  border-radius: 5px;
  border: none;
  font-family: 'Ubuntu', sans-serif;
  font-size: 16px;
  cursor: pointer;
}

.main {
  width: 60%;
  height: 75%;
  box-shadow: 0 2px 8px lightgrey;
  display: flex;
  gap: 1rem;
  background-color: $White;
  @include mobile {
    flex-direction: column;
    width: 100%;
    padding: 0;
    height: 100%;
    background-color: transparent;
    align-items: center;
  }
}

.card.navbar {
  flex: 1.3;
  background-image: url("./assets/images/bg-sidebar-desktop.svg");
  background-size: cover;
  padding: 2rem;
letter-spacing: 1px;
  ul.links {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;

    li.nav_item_card {
      display: flex;
      align-items: center;
      gap: 1rem;
      .nav_item_circle {
        border: 1px solid $White;
        border-radius: 50%;
        color: $White;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 30px;
        height: 30px;
      }

      &.active .nav_item_circle {
        background-color: $Light_blue;
        color: black;
      }
      .nav_item {
        small {
          color: $Light_gray;
        }
        p {
          color: $White;
          margin-top: 5px;
        }
      }
    }
  }
  @include mobile {
    border-radius: 0;
    width: 100%;
    background-image: url("./assets/images/bg-sidebar-mobile.svg");
    ul.links {
      width: 100%;
      justify-content: center;
      flex-direction: row;
      li.nav_item_card .nav_item_circle {
        width: 40px;
        height: 40px;
        font-size: 20px;
        font-weight: 500;
      }
      .nav_item {
        display: none;
      }
    }
  }
}
.main_content {
  flex: 3;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  position: relative;
    align-items: center;
  @include mobile {
    flex: 4;
    width: 90%;
    border-radius: 10px;
    background-color: $White;
    position: relative;
    top: -12%;
  }
  .title {
    align-self: flex-start;
    h1 {
      color: $Marine_blue;
    }
    p {
      // margin-top: 10px;
      color: $Cool_gray;
    }
  }
}

.content {
  margin-top: 2rem;
  display: flex;
  width: 100%;
  flex-direction: column;
  gap: 1rem;
  @include mobile{
    position: relative;
  }
}
.actions{
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  margin-top: 1rem;
  position: absolute;
  bottom: 1rem;
  width: 100%;
  padding: 0 4rem;
  .button.primary{
    background-color: $Marine_blue;
    color: $White;
    font-weight: 500;
    &:hover{
      background-color: hsla(213, 96%, 18%, 0.86);
    }
  }
  .button.back-btn{
    background-color: transparent;
    color: $Cool_gray;
    font-weight: 500;
    padding-left: 0;
    &:hover{
      color: $Marine_blue;
    }
  }
  .button.confirm{
      background-color: $Purplish_blue;
      color: $White;
  }
  @include mobile{
    position: absolute;
    bottom: -4rem;
    padding: 0 2rem;
  }
  .button.disabled{
    cursor: not-allowed;
    background-color: $Light_gray;
  }
}

// plans /
.content {
  .plans{
    display: flex;
    justify-content: space-around;
    gap: 1rem;
    @include mobile{
      flex-direction: column;
    }
    .card.plan{
      flex: 1;
      display: flex;
      flex-direction: column;
      border: 2px solid $Light_gray;
      transition: all .3s ease;
      cursor: pointer;
      &.active{
        border-color: $Purplish_blue ;
      }
      img{
        width:40px;
      }
      .price{
        margin-top: 2rem ;
        color: $Marine_blue;
        display: flex;
        flex-direction: column;
        gap: .5rem;
        letter-spacing: normal;
        h4{
          font-size: 16px;
        }
        span{
          color: $Light_gray;
          font-size: 14px;
        }
      }
      &:hover{
        border-color: $Purplish_blue;
        background-color: $Magnolia;
      }
      @include mobile{
        flex-direction: row;
        align-items: center;
        gap: 1rem;
        .price{
          margin-top: 0;
        }
      }
    }
  }
  .price_action{
    background-color: $Magnolia;
    padding: .5rem;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    margin-top: 1rem;
    svg{
      font-size: 40px;
      cursor: pointer;
    }
  }
}

.content{
  .card.addon{
    display: flex;
    border: 1px solid $Purplish_blue;
    gap: 1rem;
    justify-content: space-between;
    align-items: center;
    div{
      display: flex;
      gap: 1.5rem;
      cursor: pointer;
      label{        
        cursor: pointer;
        p{
          font-weight: 500;
          color: $Marine_blue;
          margin-bottom: 5px;
        }
        span{
          color: $Cool_gray;
          letter-spacing: normal;
        }
      }
    }
    &:hover , &.active{
      background-color: $Magnolia;
    }

    span.price{
      color: $Purplish_blue;
      font-weight: 500;
    }
  }
}

.card.summaryCard{
  background-color: $Alabaster;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding: 2rem;
  .item_amount{
    display: flex;
    justify-content: space-between;
    align-items: center;
    .item{
      color: $Cool_gray;
      a{
        color: $Cool_gray;
        cursor: pointer;
      }
    }
    .amount{
      color: $Marine_blue;
      font-weight: 400;
    }
    &:first-of-type{
      border-bottom: 1px solid $Light_gray;
      padding-bottom: 1rem;
      h3{
        color: $Marine_blue;
      }
      .amount{
        color: $Marine_blue;
        font-weight: 700;
      }
    }
  }
}
.total{
  margin-top: -1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: $Cool_gray;
  // padding: 1rem 2rem;
  h3{
    color: $Purplish_blue;
  }
}

.card.finished{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  gap: 1rem;
  text-align: center;
  color: $Cool_gray;
  img{
    width: 100px;
  }
  h1{
    color: $Marine_blue;
  }


}

.main_content{
  padding: 2rem 4rem;
  @include mobile{
    padding: 2rem;
  }
}

